@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700");
html,
body {
  height: 100%;
}
#kuvaify {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  font-family: 'Source Sans Pro';
  background: #101010;
  z-index: 1;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#kuvaify *,
#kuvaify *:before,
#kuvaify *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#kuvaify.visible {
  opacity: 1;
  visibility: visible;
  z-index: 2468;
}
#kuvaify a {
  transition: background 0.2s ease, opacity 0.8s ease, visibility 0.8s ease;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  overflow: hidden;
  cursor: pointer;
}
#kuvaify a svg {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 60%;
}
#kuvaify a:hover {
  background: #94d1d1;
}
#kuvaify a:before {
  display: block;
  content: '';
  width: 100%;
  height: 100%;
  transition: background 0.4s ease;
}
#kuvaify.focused a:before {
  background: rgba(0,0,0,0.1);
}
#kuvaify .kuvaify-img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity: 0;
  visibility: hidden;
  max-width: 100%;
  max-height: 100%;
  z-index: 2;
}
#kuvaify .kuvaify-img.measure {
  visibility: visible;
}
#kuvaify .kuvaify-img.visible {
  opacity: 1;
  visibility: visible;
}
#kuvaify .kuvaify-img.cover-vertically {
  max-width: none;
  max-height: none;
  width: auto;
  height: 100%;
}
#kuvaify .kuvaify-img.cover-horizontally {
  max-width: none;
  max-height: none;
  width: 100%;
  height: auto;
}
#kuvaify #kuvaify-close-button,
#kuvaify #kuvaify-menu-button,
#kuvaify #kuvaify-next-button,
#kuvaify #kuvaify-prev-button {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 6;
}
#kuvaify #kuvaify-close-button.visible,
#kuvaify #kuvaify-menu-button.visible,
#kuvaify #kuvaify-next-button.visible,
#kuvaify #kuvaify-prev-button.visible {
  opacity: 1;
  visibility: visible;
}
#kuvaify #kuvaify-close-button,
#kuvaify #kuvaify-menu-button {
  top: 0;
}
@media only screen and (min-width: 37.5em) {
  #kuvaify #kuvaify-close-button,
  #kuvaify #kuvaify-menu-button {
    top: 1rem;
  }
}
@media only screen and (min-width: 48em) {
  #kuvaify #kuvaify-close-button,
  #kuvaify #kuvaify-menu-button {
    top: 2rem;
  }
}
#kuvaify #kuvaify-close-button {
  right: 0;
}
#kuvaify #kuvaify-close-button:hover {
  background: #d65c5c;
}
#kuvaify #kuvaify-menu-button {
  right: 4rem;
}
#kuvaify #kuvaify-next-button,
#kuvaify #kuvaify-prev-button {
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
#kuvaify #kuvaify-prev-button {
  left: 0;
}
#kuvaify #kuvaify-next-button {
  right: 0;
}
#kuvaify #kuvaify-menu-div {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity: 0;
  visibility: hidden;
  width: 8rem;
  z-index: 6;
  background-color: rgba(0,0,0,0.4);
  transition: transform 0.4s ease, opacity 0.4s ease, visibility 0.4s ease;
  -webkit-transform: translate(-50%, -46%);
  -ms-transform: translate(-50%, -46%);
  transform: translate(-50%, -46%);
}
#kuvaify #kuvaify-menu-div.visible {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
@media only screen and (min-width: 37.5em) {
  #kuvaify #kuvaify-menu-div {
    width: 20rem;
  }
}
#kuvaify #kuvaify-rotate-negative-button,
#kuvaify #kuvaify-rotate-positive-button,
#kuvaify #kuvaify-zoom-out-button,
#kuvaify #kuvaify-zoom-in-button,
#kuvaify #kuvaify-reset-button {
  position: relative;
  float: left;
}
#kuvaify #kuvaify-help {
  float: left;
  width: 100%;
  padding: 1rem;
  background-color: #fff;
}
#kuvaify #kuvaify-help strong {
  color: #d65c5c;
}
#kuvaify #kuvaify-caption {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  left: 0;
  bottom: 1rem;
  overflow: hidden;
  padding: 1rem;
  background: #fff;
  z-index: 4;
}
#kuvaify #kuvaify-caption.visible {
  opacity: 1;
  visibility: visible;
}
@media only screen and (min-width: 37.5em) {
  #kuvaify #kuvaify-caption {
    bottom: auto;
    top: 1rem;
  }
}
@media only screen and (min-width: 48em) {
  #kuvaify #kuvaify-caption {
    top: 2rem;
  }
}
#kuvaify #kuvaify-caption p {
  line-height: 1;
  white-space: nowrap;
}
#kuvaify #kuvaify-close-div {
  width: 100%;
  height: 100%;
}
#kuvaify #kuvaify-spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity: 0;
  visibility: hidden;
  padding: 1rem;
  background: #101010;
  transition: opacity 0.4s ease;
}
#kuvaify #kuvaify-spinner.visible {
  opacity: 1;
  visibility: visible;
}
#kuvaify .sk-folding-cube {
  width: 40px;
  height: 40px;
  position: relative;
  -webkit-transform: rotateZ(45deg);
  -ms-transform: rotateZ(45deg);
  transform: rotateZ(45deg);
}
#kuvaify .sk-folding-cube .sk-cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}
#kuvaify .sk-folding-cube .sk-cube:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #d65c5c;
  -webkit-animation: sk-foldCubeAngle 2.4s infinite linear both;
  animation: sk-foldCubeAngle 2.4s infinite linear both;
  -webkit-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
}
#kuvaify .sk-folding-cube .sk-cube2 {
  -webkit-transform: scale(1.1) rotateZ(90deg);
  -ms-transform: scale(1.1) rotateZ(90deg);
  transform: scale(1.1) rotateZ(90deg);
}
#kuvaify .sk-folding-cube .sk-cube3 {
  -webkit-transform: scale(1.1) rotateZ(180deg);
  -ms-transform: scale(1.1) rotateZ(180deg);
  transform: scale(1.1) rotateZ(180deg);
}
#kuvaify .sk-folding-cube .sk-cube4 {
  -webkit-transform: scale(1.1) rotateZ(270deg);
  -ms-transform: scale(1.1) rotateZ(270deg);
  transform: scale(1.1) rotateZ(270deg);
}
#kuvaify .sk-folding-cube .sk-cube2:before {
  animation-delay: 0.3s;
}
#kuvaify .sk-folding-cube .sk-cube3:before {
  animation-delay: 0.6s;
}
#kuvaify .sk-folding-cube .sk-cube4:before {
  animation-delay: 0.9s;
}
@-webkit-keyframes sk-foldCubeAngle {
  0%, 10% {
    -webkit-transform: perspective(140px) rotateX(-180deg);
    -ms-transform: perspective(140px) rotateX(-180deg);
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    -webkit-transform: perspective(140px) rotateX(0deg);
    -ms-transform: perspective(140px) rotateX(0deg);
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    -webkit-transform: perspective(140px) rotateY(180deg);
    -ms-transform: perspective(140px) rotateY(180deg);
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}
@-moz-keyframes sk-foldCubeAngle {
  0%, 10% {
    -webkit-transform: perspective(140px) rotateX(-180deg);
    -ms-transform: perspective(140px) rotateX(-180deg);
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    -webkit-transform: perspective(140px) rotateX(0deg);
    -ms-transform: perspective(140px) rotateX(0deg);
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    -webkit-transform: perspective(140px) rotateY(180deg);
    -ms-transform: perspective(140px) rotateY(180deg);
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}
